    async function uploadImage(event,parent){
        // console.log('event :: ',event.target.);
        
        const imageObject=event.target.files[0];
        const fileSize = event.target.files[0].size/1024
        console.log("event.target.files[0]",event.target.files[0].size)
        // console.log(imageObject.name);
        if(fileSize<=250){
            try {

                const isPortal = builderMode === "application";

                if(isPortal || activePage || activeSubForm) {
                    const jsonToSend = {
                        imageObject,
                        applicationName:
                          microFrontendData?.processName ||
                          microFrontendData?.applicationName ||
                          microFrontendData?.processDefId ||
                          "",
                        formName:
                          microFrontendData?.taskId ||
                          microFrontendData?.formName ||
                          activePage?.formName ||
                          activeSubForm?.fragmentName ||
                          microFrontendData?.formId ||
                          microFrontendData?.formDefId ||
                          "",
                      };
                    const response = await uploadImageAPI(jsonToSend);
                    handleImageUploading(imageObject.name, response.data, "page");
                } else {
                    const imageBase64=await convertBase64(imageObject);
                    handleImageUploading(imageObject.name, imageBase64, "page");
                }
    
                // const imgSrc = `/formBuilder/idesign/getImage/${jsonToSend.applicationName}/${jsonToSend.formName}/${response.data}`;

                // const imgSrc = `/formBuilder/idesign/getImage/PortalImage/Page 1/${response.data}`;

                // handleImageUpload(imageObject.name, imgSrc, uploadindex);
            } catch (error) {
                showAlertConfirmNotifications({
                    open: true,
                    showConfirm: "showNotification",
                    alertType: "error",
                    alertContentKey: error || "Failed to upload Image.",
                });
            }
        } else {
            showAlertConfirmNotifications({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentKey: "Image size should not be greater than 250kb",
              });
        }
        // props.handleImageUpload(imageBase64,imageObject.name, props.tabChild, props.tabNo,action);
        // } else if(action==="remove"){          
        // handleImageUpload("","","page");
        //     // props.handleImageUpload("imageBase64","name", props.tabChild, props.tabNo,action);
        // }
        event.target=null
    }
    // console.log(props,'PagePropert====>>>', pageProperties)
    const convertBase64 =(file) => {
        return new Promise((resolve,reject) => {
          const fileReader=new FileReader();
          fileReader.readAsDataURL(file);
          fileReader.onload = () => {
            resolve(fileReader.result);
          };
  
          fileReader.onerror =(error) => {
            reject(error);
          };
        });
      };

     function handleImageUploading(imageName,imageFile,parent){
         
        setBackgroundImageFileName(imageName);
        setShowUploadButton(false);
        setShowImageFileName(true);
        setBackgroundImage(imageFile );
        saveProperties("save","backgroundImageFileName",imageName,parent);
        saveProperties("save","backgroundImage", `url("${imageBaseURL}${imageFile}")`, parent );
        saveProperties("save","backgroundRepeat","no-repeat", parent);
        saveProperties("save","backgroundPosition","center", parent);
        var temp_PageProperties = pageProperties;
        temp_PageProperties["pageBackground"]["selected"] = "backgroundImage";
        temp_PageProperties["pageBackground"]["backgroundImage"] = `url("${imageBaseURL}${imageFile}")`;
        temp_PageProperties["pageBackground"]["backgroundColor"] = "";
        props.selectedControl.pageStyle["backgroundColor"]="";
        props.selectedControl.pageStyle["selected"] ="backgroundImage"
        props.selectedControl.pageStyle["backgroundImage"] =`url("${imageBaseURL}${imageFile}")`
        props.selectedControl.pageStyle["backgroundImageFileName"] =imageName
        props.selectedControl.pageStyle["backgroundRepeat"] ="no-repeat"
        props.selectedControl.pageStyle["backgroundPosition"] ="center"

        setPageProperties(temp_PageProperties);
        updatedComponentInFormState()
        
     }

